dsfgds :root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

body {
  color: #212121;
  background-color: #fafafa;
  line-height: 1.5;
}

fieldset {
  width: 100%;
  z-index: 1;
  background-color: #4056b4;
  border: none;
  padding: 10px 0;
  position: fixed;
  top: 0;
}

.search-form {
  width: 50%;
  max-width: 200px;
  margin-left: auto;
  margin-right: auto;
  display: block;
  position: relative;
}

input[type="text"] {
  width: 100%;
  vertical-align: middle;
  height: 16px;
  border: 1px solid #aaa;
  border-radius: 2px;
  padding: 2px 20px 2px 2px;
  font-family: Arial, Helvetica, sans-serif;
}

button[type="submit"] {
  cursor: pointer;
  text-indent: -999px;
  height: 16px;
  width: 16px;
  background: url("search.f8c09802.svg") 1px no-repeat;
  border: 0;
  position: absolute;
  top: 5px;
  left: calc(100% + 4px);
  overflow: hidden;
}

.gallery {
  max-width: 1140px;
  grid-gap: 12px;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  grid-auto-rows: 240px;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  padding: 60px;
  display: grid;
}

.photo-card:hover {
  transform: scale(1.03);
}

.photo-card {
  transition: transform var(--animation-duration) var(--timing-function);
  border: 1px solid #aaa;
  border-radius: 0 0 4px 4px;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

img {
  height: 70%;
  width: 100%;
  object-fit: cover;
}

.info {
  height: 100%;
  justify-content: space-around;
  font-size: 14px;
  display: flex;
}

.info-item {
  flex-direction: column;
  align-items: center;
  font-family: Arial, Helvetica, sans-serif;
  display: flex;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.more-block {
  justify-content: center;
  display: flex;
}

.load-more {
  color: #fafafa;
  cursor: pointer;
  background-color: #4056b4;
  border: none;
  border-radius: 4px;
  margin: 10px;
  padding: 6px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 16px;
  display: block;
}

.notification-text {
  letter-spacing: 1.02px;
  padding: 20px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 16px;
}

